.search-form input {
  height: 25px;
  font-size: 18px;
  display: block;
  margin: 0 auto;
}

.gallery {
  list-style: none;
  padding: 0;
  margin: 0;
  width: 1440px;

  display: flex;
  flex-wrap: wrap;
  margin: 0 auto;
}

.photo-card {
  width: 400px;
  margin-top: 30px;
  margin-left: 30px;
}
.photo-card img {
  display: block;
  width: 100%;
}

.stats,
.stats-item {
  display: flex;
  justify-content: center;
}

.material-icons {
  margin-right: 10px;
}

.stats-item:not(:last-child) {
  margin-right: 15px;
}
